.homeContainer {
    display: flex;
    width: 100%;
    border-color: #f8fcff;
}
.rightbar {
    flex: 3.5 1;
}

.rightbarWrapper {
    padding: 20px 20px 0 0;
}

.eventContainer {
    display: flex;
    align-items: center;
}

.starImg {
    width: 40px;
    height: 40px;
    margin-right: 5px;
}

.eventText {
    font-weight: 300;
    font-size: 15px;
}

.eventImg {
    width: 100%;
    border-radius: 10px;
    margin: 25px 0;
}

.rightbarTitle {
    margin-bottom: 20px;
}

.rightbarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.rightbarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.rightbarProfileImgContainer {
    margin-right: 10px;
    position: relative;
}

.rightbarProfileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.rightbarUsername {
    font-weight: 550;
}

.rightbarOnline {
    background-color: purple;
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    top: -2px;
    left: 0;
    border: 2px solid white;
}

.promotionTitle {
    font-size: 18px;
    font-weight: 550;
    margin-bottom: -10px;
}

.rightbarPromotionImg {
    width: 85%;
    border-radius: 10px;
    margin: 25px 0;
}

.promotionName {
    font-size: small;
    color: gray;
    margin-top: -20px;
}

.rightbarTitle {
    font-size: 18px;
    font-weight: 550;
    margin-bottom: 10px;
}

.rightbarInfo {
    margin-bottom: 30px;
}

.rightbarinfoKey {
    font-weight: 500;
    margin-right: 5px;
    color: #555;
}

.rightbarFollowings {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.rightbarFollowing {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}

.rightbarInfoItem {
    margin-bottom: 10px;
}

.rightbarFollowingImg {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
}
.sidebar {
    flex: 2.5 1;
    height: 100vh;
}

.sidebarWrapper {
    padding: 20px;
}

.sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarFriendImg {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
}

.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 4px 6px;
    cursor: pointer;
    box-shadow: 3px 7px 13px -5px #65789f;
    border-radius: 10px;
}

.sidebarListItem:hover {
    box-shadow: none;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    transition: all 0.3s;
}

.sidebarIcon {
    font-size: 35px !important;
    margin-right: 10px;
}

.sidebarListItemText {
    font-size: 17px;
    padding-top: 3px;
}

.sidebarHr {
    margin: 20px 0;
}

.sidebarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.post {
    width: 100%;
    box-shadow: 3px 7px 13px -5px #65789f;
    border-radius: 10px;
    margin: 30px 0;
}

.postWrapper {
    padding: 10px;
}

.postProfileImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}

.postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postTopLeft {
    display: flex;
    align-items: center;
}

.postUsername {
    font-size: 15px;
    font-weight: 550;
    margin: 0 10px;
}

.postDate {
    font-size: 12px;
}

.postCenter {
    margin: 20px 0;
}

.postImg {
    margin-top: 20px;
    width: 100%;
    max-height: 500px;
    object-fit: contain;
}

.postBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postBottomLeft {
    display: flex;
    align-items: center;
}

.likeIcon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    cursor: pointer;
}

.postLikeCounter {
    font-size: 15px;
}

.postCommentText {
    cursor: pointer;
    border-bottom: 1px solid gray;
    font-size: 15px;
    margin-right: 3px;
}
.share {
    width: 100%;
    height: 170px;
    box-shadow: 3px 7px 13px -5px #65789f;
    border-radius: 10px;
}

.shareWrapper {
    padding: 10px;
}

.shareTop {
    display: flex;
    align-items: center;
}

.shareProfileImg {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.shareInput {
    border: none;
    width: 100%;
}

.shareInput:focus {
    outline: none;
}

.shareHr {
    margin: 20px;
}

.shareButtons {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.shareOptions {
    display: flex;
    margin-left: 20px;
}

.shareOption {
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}

.shareIcon {
    margin-right: 3px;
}

.shareOptionTExt {
    font-size: 14px;
    font-weight: 550;
}

.shareButton {
    border: none;
    padding: 6px 17px;
    background-color: #2c517c;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}
.timeline {
    flex: 6 1;
}

.timelineWrapper {
    padding: 30PX 170px;
}
.topbarContainer {
    background-color: #2c517c;
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
}

.topbarLeft {
    flex: 3 1;
}

.logo {
    font-size: 24px;
    color: white;
    font-weight: bold;
    margin-left: 20px;
}

.searchbar {
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 25px;
    display: flex;
    align-items: center;
}

.searchIcon {
    font-size: 20px !important;
    margin-left: 10px;
    margin-right: 5px;
}

.searchInput {
    border: none;
    width: 80%;
}

.searchInput:focus {
    outline: none;
}

.topbarCenter {
    flex: 5 1;
}

.topbarRight {
    flex: 4 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.topbarItemIcons {
    display: flex;
}

.topbarIconItem {
    margin-right: 15px;
    cursor: pointer;
    position: relative;
    margin-top: 5px;
}

.topbarImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    cursor: pointer;
    object-fit: cover;
}

.topbarIconBadge {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: #db6ab9;
    width: 15px;
    height: 15px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px
}
.profile {
    display: flex;
}

.profileRight {
    flex: 10.5 1;
}

.profileCover {
    height: 320px;
    position: relative;
}

.profileCoverImg {
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileUserImg {
    position: absolute;
    left: 0;
    right: 0;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: auto;
    top: 150px;
}

.profileInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profileInfoName {
    font-size: 24px;
}

.profileRightBottom {
    display: flex;
}
.login {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
}

.loginLeft,
.loginRight {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginLogo {
    font-size: 50px;
    font-weight: 800;
    color: #41428b;
    margin-bottom: 10px;
}

.loginDesc {
    font-size: 24px;
}

.loginBox {
    height: 300px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-direction: column;
}

.loginInput {
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}

.loginInput:focus {
    outline: none;
}

.loginButton {
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #41428b;
    color: white;
    font-size: 20px;
    cursor: pointer;
}

.loginForgot {
    text-align: center;
    color: #41428b;
}

.loginRegisterButton {
    height: 50px;
    border-radius: 10px;
    align-self: center;
    border: none;
    background-color: #3c8b50;
    color: white;
    font-size: 20px;
    cursor: pointer;
}

.loginMsg {
    text-align: center;
    font-weight: 550;
    font-size: 20px;
    margin-bottom: 5px;
}
